import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

import Box from '@material-ui/core/Box';





import '../fonts/fonts.css'

const useStyles = makeStyles({
	root: {
		maxWidth: 220,
	},
	minWidth: {
		width: '100%',
	},
	titleH6: {
		lineHeight: 1.2,
		textAlign: 'center',
	},
	marginCard: {
		margin: '0 0 8px 0',
	}
});



const CastomCard = () => {

	const classes = useStyles();

    return (
		<Box className={classes.root}>
			<Card className={classes.minWidth, classes.marginCard}>
				<Box display="flex" justifyContent="center" p={1}>
					<div className="geek-dust-bunny-s"></div>
				</Box>
				<Box p={1}>
					<Typography className={classes.titleH6} variant="h6" component="h2">
						.geek-dust-bunny
					</Typography>
				</Box>
			</Card>
			<Button className={classes.minWidth} color="primary" variant="contained" href="https://codepen.io/kapustiansky/pen/zYBgLmV">
				SVG on Codepen
			</Button>
		</Box>
    );
  }

export default CastomCard;